.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.card {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.name {
  font-size: 24px;
  font-weight: bold;
}

.title {
  font-size: 16px;
  color: #888888;
}

.content {
  font-size: 14px;
}

.location {
  margin-bottom: 10px;
}

.phone,
.email,
.linkedin {
  margin-bottom: 5px;
}

a {
  color: #0066cc;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.section-title {
  font-weight: bold;
  margin-bottom: 5px;
}

ul {
  margin: 0;
  padding-left: 20px;
}

li {
  margin-bottom: 5px;
}

.tabs {
  display: flex;
}

.tab {
  flex: 1;
  padding: 10px;
  cursor: pointer;
  text-align: center;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
}

.tab:hover {
  background-color: #e0e0e0;
}

.active {
  font-weight: bold;
  color: #333;
}

.json-tab-content.active,
.faker-tab-content.active {
  display: block;
}
